import { defineComponent } from "vue";
import { IonBadge } from "@ionic/vue";
import useEvent from "@/hooks/useEvent";
export default defineComponent({
  name: "SearchBadge",
  components: {
    IonBadge
  },
  props: {
    title: String,
    event: String,
    cancelable: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const emmiter = useEvent();
    const emit = () => {
      emmiter.emit(props.event);
    };
    return {
      emit
    };
  }
});