import { defineComponent, toRefs } from "vue";
import { IonCardContent, IonCardHeader, IonCardSubtitle, IonCardTitle } from "@ionic/vue";
import LikeManager from "@/functional/CarPost/LikeManager.vue";
import { roundOf } from "@/lib/utils";
import { PostDisplay } from "@/model/Posts";
export default defineComponent({
  name: "CarPostCard",
  components: {
    IonCardHeader,
    IonCardTitle,
    IonCardContent,
    IonCardSubtitle,
    LikeManager
  },
  props: {
    carPost: {
      type: PostDisplay,
      required: true
    },
    button: {
      type: Boolean,
      default: false
    },
    like: {
      type: Boolean,
      default: true
    }
  },
  setup(props) {
    const {
      carPost
    } = toRefs(props);
    const carSpec = carPost.value.car;
    const post_id = carPost.value.post_id;
    const mileage = carPost.value._mileage;
    const transmission = carPost.value["transmission"];
    return {
      roundOf,
      carSpec,
      post_id,
      mileage,
      transmission
    };
  }
});